import http from '@/services/http'
import { updateTimeout } from '@/consts/timeouts'

let timeoutJob = null

export default {
  namespaced: true,
  state: {
    loading: true,
    wasError: false
  },
  mutations: {
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    SET_WAS_ERROR(state, wasError) {
      state.wasError = wasError
    }
  },
  actions: {
    async checkJob({ dispatch, commit }) {
      await dispatch('cancelCheckJob')
      
      try {
        const res = await http.get('check/job')

        /**
         * –°—Ç—Ä–æ–≥–æ–µ —Å—Ä–∞–≤–Ω–µ–Ω–∏–µ, —Ç–∞–∫ –∫–∞–∫
         * –µ—Å–ª–∏ –∑–∞–ø—Ä–æ—Å –∑–∞–ø—É—â–µ–Ω —Ç–æ false,
         * –µ—Å–ª–∏ –≤—ã–ø–æ–ª–Ω–µ–Ω true
         * –µ—Å–ª–∏ null –ø—Ä–∏—Ö–æ–¥–∏—Ç –∑–Ω–∞—á–∏—Ç –µ—â–µ –Ω–∏–∫—Ç–æ –Ω–∏—á–µ–≥–æ –Ω–µ –∑–∞–ø—É—Å–∫–∞–ª
         */

        if (res.checkJob === false) {
          commit('SET_LOADING', true)
        } else {
          commit('SET_LOADING', false)
        }
        timeoutJob = setTimeout(() => {
          dispatch('checkJob')
        }, updateTimeout)
      } catch(e) {
        if (!e.response) {
          commit('SET_LOADING', false)
        }
        throw e
      }
    },
    cancelCheckJob() {
      if (timeoutJob) {
        clearTimeout(timeoutJob)
        timeoutJob = null
      }
    },
    async updateCameras({ dispatch, commit }) {
      try {
        commit('SET_WAS_ERROR', false)
        commit('SET_LOADING', true)
        await http.get('cameras', {
          params: {
            updateCameras: true 
          }
        })
      } catch(e) {
        commit('SET_WAS_ERROR', true)
      } finally {
        dispatch('checkJob')
      }
    }
  }
}