import { computed } from '@vue/composition-api'
import { useI18n } from 'vue-i18n-composable/dist/index.js'

/**
 * 
 * @param {*} moment - –æ–±—ä–µ–∫—Ç moment.js - –±—Ä–∞—Ç—å –∏–∑ context.root
 */

export const useStatusDate = moment => computed(() => date => {
  const i18n = useI18n()
  const format = i18n.locale.value === 'ru' ? 'DD.MM.YYYY' : 'MM.DD.YYYY'
  return moment(date).format(format)
})

export const formatNumber = (num) => {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
}

/**
 * 
 * @param {*} dateStr - —Å—Ç—Ä–æ–∫–∞ –≤–∏–¥–∞ —á–∞—Å—ã:–º–∏–Ω—É—Ç—ã
 */
export const statisticTimeFormat = (dateStr) => {
  if (dateStr) {
    const dateStrArr = dateStr.split(':')
    return {
      hours: +dateStrArr[0],
      minutes: +dateStrArr[1]
    }
  }
  return null
}



